@import 'https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap';
.header {
  background-color: #000;
  background-image: linear-gradient(
      to bottom,
      rgba(26, 26, 31, 0.5) 20%,
      rgba(0, 0, 0, 0.56) 90%
    ),
    url(../images/header1.jpg);
  background-size: cover;
  font-family: 'Roboto', sans-serif;
  height: 230px;
}
.header__container {
  padding: 40px 70px 30px 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__page-name {
  font-weight: 500;
  font-size: 30px;
  line-height: 1.1;
  align-content: center;
  text-decoration: none;
  color: #fff;
}
.header__icon {
  stroke: #818181;
  fill: rgba(0, 0, 0, 0);
  margin-right: 2px;
}
.header__list {
  display: flex;
  justify-content: space-between;
  list-style: none;
  gap: 20px;
}
.header__items {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.1;
  align-content: center;
  text-decoration: none;
  color: #fff;
  text-transform: uppercase;
}
.active::after {
  display: flex;
  content: '';
  width: 100%;
  height: 3px;
  border-radius: 2px;
  background-color: #ff6b08;
  cursor: auto;
}
.header__searcher {
  font-size: 14px;
  line-height: 1.1;
  background-color: rgba(0, 0, 0, 0);
  width: 336px;
  border-top: none;
  border-left: none;
  border-right: none;
  color: #fff;
  outline: none;
  cursor: pointer;
}
.header__searcher::-moz-placeholder {
  color: #fff;
}
.header__searcher::placeholder {
  color: #fff;
}
.input__icon {
  stroke: #fff;
  fill: rgba(0, 0, 0, 0);
  position: relative;
  left: -20px;
  cursor: pointer;
}
.form {
  display: flex;
  justify-content: center;
}
.form__button {
  border: none;
  background-color: rgba(0, 0, 0, 0);
}
@media (min-width: 471px) and (max-width: 768px) {
  .header {
    background-image: linear-gradient(
        to bottom,
        rgba(26, 26, 31, 0.5) 20%,
        rgba(0, 0, 0, 0.56) 90%
      ),
      url(../images/header2.jpg);
  }
}
@media (max-width: 470px) {
  .header__container {
    padding: 40px 50px 30px 50px;
  }
  .header {
    background-image: linear-gradient(
        to bottom,
        rgba(26, 26, 31, 0.5) 20%,
        rgba(0, 0, 0, 0.56) 90%
      ),
      url(../images/header3.jpg);
  }
  .header__page-name {
    display: none;
  }
  .header__searcher {
    width: 280px;
  }
}
.header__library {
  background-color: #000;
  background-image: linear-gradient(
      to bottom,
      rgba(26, 26, 31, 0.5) 20%,
      rgba(0, 0, 0, 0.56) 90%
    ),
    url(../images/library-header.png);
  background-size: cover;
  background-position: center bottom;
  font-family: 'Roboto', sans-serif;
  height: 230px;
}
.header__container-library {
  padding: 40px 70px 10px 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.library-header__button {
  text-transform: uppercase;
  font-size: 12px;
  color: #fff;
  background: rgba(0, 0, 0, 0);
  border-radius: 5px;
  border-color: #fff;
  cursor: pointer;
  height: 44px;
  width: 136px;
}
.library-header__button:hover,
.library-header__button:focus {
  border: none;
  background-color: #ff6b01;
}
.library-header__buttons-list {
  display: flex;
  justify-content: center;
  list-style: none;
  gap: 15px;
  margin-bottom: 20px;
}
@media (max-width: 470px) {
  .header__container-library {
    padding: 40px 50px 30px 50px;
  }
  .library-header__buttons-list {
    padding: 0;
  }
} 